import './App.css'
import React from 'react'
import Modal from 'react-modal'

import {
    Button,
    FormControl,
    Input,
    InputAdornment,
    InputLabel,
    makeStyles,
    MenuItem,
    Select,
} from '@material-ui/core'
import VehiclePageTable from './VehiclePageTable.jsx'

const useStyles = makeStyles((theme) => ({
    root: {
        '& .MuiTextField-root': {
            color: 'white',
        },
        '& .MuiTypography-colorTextSecondary': {
            color: 'white',
        },
        '& .MuiInputLabel-formControl': {
            color: 'white',
        },
        '& .MuiFilledInput-input': {
            color: 'white',
        },
    },
}))

function TopBar(props) {
    const { name, doRankAction, values, handleVal } = props
    const classes = useStyles()
    return (
        <div className="SearchBar">
            <div style={{ fontFamily: 'DM Mono', fontSize: '24px' }}>
                {name}
            </div>
            <div style={{ fontFamily: 'DM Mono', fontSize: '24px' }}>
                <Button
                    style={{ backgroundColor: 'rgb(37 146 81)' }}
                    onClick={() => {
                        doRankAction('buyVehicle')
                    }}
                >
                    BUY VEHICLE
                </Button>
            </div>
            <div>
                <FormControl fullWidth className={classes.root}>
                    <InputLabel htmlFor="standard-adornment-amount">
                        Plate
                    </InputLabel>
                    <Input
                        style={{ color: 'white' }}
                        id="standard-adornment-amount"
                        value={values.searchBar}
                        onChange={(event) => {
                            handleVal(event.target.value)
                        }}
                        startAdornment={
                            <InputAdornment position="start">üìü</InputAdornment>
                        }
                    />
                </FormControl>
            </div>
        </div>
    )
}

var moneyFormatter = new Intl.NumberFormat('en-US', {
    style: 'currency',
    currency: 'USD',
})

function BuyVehicleModal(props) {
    const {
        closeModal,
        values,
        buyVehicle,
        currVehOption,
        setCurrVehOption,
    } = props

    const classes = useStyles()
    return (
        <div style={{ height: '100%', weight: '100%' }}>
            <div className="ModalTopbar">
                <Button
                    style={{ backgroundColor: 'rgb(146, 37, 37)' }}
                    onClick={closeModal}
                >
                    Close
                </Button>
                <div>Buy Vehicle</div>
            </div>
            <div
                style={{
                    marginTop: '15%',
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'center',
                }}
            >
                <FormControl
                    className={classes.root}
                    style={{ width: '100%' }}
                    variant="filled"
                >
                    <InputLabel id="demo-simple-select-placeholder-label-label">
                        Vehicle
                    </InputLabel>
                    <Select
                        labelId="demo-simple-select-placeholder-label-label"
                        id="demo-simple-select-placeholder-label"
                        value={currVehOption}
                        onChange={(event) => {
                            setCurrVehOption(event.target.value)
                        }}
                        displayEmpty
                    >
                        {values.vehicleOptions.map((vehicle, index) => (
                            <MenuItem value={vehicle.id} key={vehicle.id}>
                                [{moneyFormatter.format(vehicle.cost)}]{' '}
                                {vehicle.name}
                            </MenuItem>
                        ))}
                    </Select>
                </FormControl>
            </div>
            <div
                style={{
                    marginTop: '16px',
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'center',
                }}
            >
                <Button
                    style={{ width: '100%', backgroundColor: 'rgb(37 146 81)' }}
                    onClick={(event) => {
                        buyVehicle()
                    }}
                >
                    BUY
                </Button>
            </div>
        </div>
    )
}

const sortVehicles = function (a, b) {
    if (a.vin < b.vin) {
        return 1
    }
    if (a.vin > b.vin) {
        return -1
    }
    return 0
}

const sortVehOptions = (a, b) => {
    if (a.cost < b.cost) {
        return 1
    }
    if (a.cost > b.cost) {
        return -1
    }
    return 0
}

function Page(props) {
    const { doNuiAction } = props

    const [rows, setRows] = React.useState([])
    const [cars, setCars] = React.useState([])

    const [values, setValues] = React.useState({
        vehicleOptions: [],
        searchBar: '',
    })

    const [currVehOption, setCurrVehOption] = React.useState(1)

    function setValue(name, value) {
        console.log(`Set ${name} to ${value}`)
        setValues({ ...values, [name]: value })
    }

    function reloadVehicles() {
        doNuiAction(
            'getVehicles',
            [],
            [
                {
                    vin: 1,
                    name: 'Police Crown Victoria',
                    plate: 'THELAW12',
                    inGarage: 1,
                },
                {
                    vin: 2,
                    name: 'Police Crown Victoria',
                    plate: 'NOPLATE1',
                    inGarage: 1,
                },
                {
                    vin: 3,
                    name: 'Police Taurus',
                    plate: '8REV1XF2',
                    inGarage: 2,
                },
            ]
        ).then((resp) => {
            let parsed = []
            for (let i = 0; i < resp.length; i++) {
                let curr = resp[i]
                let str
                switch (curr.inGarage) {
                    case 1:
                        str = 'Outside'
                        break
                    case 2:
                        str = 'In Garage'
                        break
                    case 3:
                        str = 'Impounded'
                        break
                    case 4:
                        str = 'Destroyed'
                        break
                    default:
                        str = 'Unknown'
                        break
                }
                curr.inGarage = str
                parsed.push(curr)
            }
            setCars(parsed.sort(sortVehicles))
            setRows(parsed.sort(sortVehicles))

            doNuiAction(
                'getVehicleOptions',
                [],
                [
                    {
                        id: 1,
                        name: 'Police Crown Victoria',
                        cost: 100000,
                    },
                    {
                        id: 2,
                        name: 'Police Crown Victoria',
                        cost: 240000,
                    },
                    {
                        id: 3,
                        name: 'Police Taurus',
                        cost: 150000,
                    },
                    {
                        id: 4,
                        name: 'Scorcher',
                        cost: 2500,
                    },
                ]
            ).then((resp) => {
                setCurrVehOption(resp[0].id)
                setValue('vehicleOptions', resp.sort(sortVehOptions))
            })
        })
    }

    const targetVehicle = React.useRef()
    const [currentModal, setCurrentModal] = React.useState(false)

    function doRankAction(action, data) {
        if (action === 'buyVehicle') {
            targetVehicle.current = data

            //setCurrVehOption(values.vehicleOptions[0].id)
            setCurrentModal(action)
        }
    }

    function closeModal() {
        setCurrentModal(false)
    }

    React.useEffect(() => {
        reloadVehicles()
    }, []) // eslint-disable-line

    function buyVehicle() {
        closeModal()
        doNuiAction('buyVehicle', { id: currVehOption }, []).then(() => {
            reloadVehicles()
        }, true)
    }

    function handleVal(newVal) {
        setValue('searchBar', newVal)

        if (newVal.length === 0) {
            setRows(cars.sort(sortVehicles))
        } else {
            let currRows = []
            for (let i = 0; i < cars.length; i++) {
                if (
                    cars[i].plate.toLowerCase().includes(newVal.toLowerCase())
                ) {
                    currRows.push(cars[i])
                }
            }
            setRows(currRows.sort(sortVehicles))
        }
    }

    return (
        <div className="FactionMembersMain">
            <div
                className="PageSectionContainer"
                style={{ width: '100%', height: '10%', alignItems: 'center' }}
            >
                <TopBar
                    name="Vehicles"
                    values={values}
                    doRankAction={doRankAction}
                    handleVal={handleVal}
                />
            </div>
            <div className="PageSectionPadding" />
            <div
                className="PageSectionContainer"
                style={{ width: '100%', height: '90%' }}
            >
                <div
                    style={{
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'center',
                    }}
                >
                    <Modal
                        className="Modal"
                        isOpen={currentModal !== false}
                        onRequestClose={closeModal}
                        style={{
                            overlay: {
                                backgroundColor: 'rgba(255, 255, 255, 0)',
                            },
                        }}
                        ariaHideApp={false} // FIX WEIRD ERRORS
                        animation={false}
                    >
                        <div className="ModalContainer">
                            {currentModal === 'buyVehicle' && (
                                <BuyVehicleModal
                                    closeModal={closeModal}
                                    values={values}
                                    setValue={setValue}
                                    buyVehicle={buyVehicle}
                                    currVehOption={currVehOption}
                                    setCurrVehOption={setCurrVehOption}
                                />
                            )}
                        </div>
                    </Modal>
                </div>

                <VehiclePageTable rows={rows} doRankAction={doRankAction} />
            </div>
        </div>
    )
}

export default Page
