function TopBar(props) {
    const { name, doRankAction } = props
    return (
        <div className="SearchBar">
            <div style={{ fontFamily: 'DM Mono', fontSize: '24px' }}>
                {name}
            </div>
            <div style={{ fontFamily: 'DM Mono', fontSize: '24px' }}>
                <Button
                    style={{ backgroundColor: 'rgb(37 146 81)' }}
                    onClick={() => {
                        doRankAction('addRank')
                    }}
                >
                    ADD RANK
                </Button>
            </div>
        </div>
    )
}

function Page(props) {
    const { doNuiAction } = props

    const [rows, setRows] = React.useState([])

    const [values, setValues] = React.useState({
        alias: '',
        newMemberCID: '0',
    })

    function setValue(name, value) {
        console.log(`Set ${name} to ${value}`)
        setValues({ ...values, [name]: value })
    }

    function reloadRanks() {
        doNuiAction(
            'getFactionRanks',
            [],
            [
                {
                    id: 1,
                    rank_name: 'Cadet',
                    rank_level: 1,
                },
                {
                    id: 2,
                    rank_name: 'Officer',
                    rank_level: 2,
                },
                {
                    id: 3,
                    rank_name: 'Senior Officer',
                    rank_level: 3,
                },
                {
                    id: 4,
                    rank_name: 'Sergeant',
                    rank_level: 4,
                },
            ]
        ).then((resp) => {
            setRows(resp.sort(sortRanks))
        })
    }

    const targetRank = React.useRef()
    const [currentModal, setCurrentModal] = React.useState(false)

    const sortRanks = function (a, b) {
        if (a.rank_level < b.rank_level) {
            return 1
        }
        if (a.rank_level > b.rank_level) {
            return -1
        }
        return 0
    }

    function doRankAction(action, data) {
        console.table(data)
        if (action === 'removeRank') {
            doNuiAction('removeRank', { name: data.rank_name }, []).then(() => {
                reloadRanks()
            })
        } else if (action === 'addRank') {
            setCurrentModal('addRank')
        } else if (action === 'changeRankName') {
            targetRank.current = data
            setValue('newRankName', data.rank_name)
            setCurrentModal('changeRankName')
        } else if (action === 'changeRankLevel') {
            targetRank.current = data
            setValue('newRankLevel', data.rank_level)
            setCurrentModal('changeRankLevel')
        }
    }

    function closeModal() {
        setCurrentModal(false)
    }

    React.useEffect(() => {
        reloadRanks()
    }, []) // eslint-disable-line

    function addRank() {
        closeModal()
        doNuiAction(
            'addRank',
            { name: values.newRankName, level: values.newRankLevel },
            []
        ).then(() => {
            reloadRanks()
        })
    }

    function changeRankName() {
        closeModal()
        doNuiAction(
            'changeRankName',
            { rank: targetRank.current, name: values.newRankName },
            []
        ).then(() => {
            reloadRanks()
        }, true)
    }

    function changeRankLevel() {
        closeModal()
        doNuiAction(
            'changeRankLevel',
            { rank: targetRank.current, level: values.newRankLevel },
            []
        ).then(() => {
            reloadRanks()
        }, true)
    }

    return (
        <div className="FactionMembersMain">
            <div
                className="PageSectionContainer"
                style={{ width: '100%', height: '10%', alignItems: 'center' }}
            >
                <TopBar
                    name="Ranks"
                    values={values}
                    doRankAction={doRankAction}
                />
            </div>
            <div className="PageSectionPadding" />
            <div
                className="PageSectionContainer"
                style={{ width: '100%', height: '90%' }}
            >
                <div
                    style={{
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'center',
                    }}
                >
                    <Modal
                        className="Modal"
                        isOpen={currentModal !== false}
                        onRequestClose={closeModal}
                        style={{
                            overlay: {
                                backgroundColor: 'rgba(255, 255, 255, 0)',
                            },
                        }}
                        ariaHideApp={false} // FIX WEIRD ERRORS
                        animation={false}
                    >
                        <div className="ModalContainer">
                            {currentModal === 'changeRankLevel' && (
                                <ChangeRankLevelModal
                                    closeModal={closeModal}
                                    values={values}
                                    setValue={setValue}
                                    changeRankLevel={changeRankLevel}
                                />
                            )}
                            {currentModal === 'changeRankName' && (
                                <ChangeRankNameModal
                                    closeModal={closeModal}
                                    values={values}
                                    setValue={setValue}
                                    changeRankName={changeRankName}
                                />
                            )}
                            {currentModal === 'addRank' && (
                                <AddRankModal
                                    closeModal={closeModal}
                                    values={values}
                                    setValue={setValue}
                                    addRank={addRank}
                                />
                            )}
                        </div>
                    </Modal>
                </div>

                <RankPageTable rows={rows} doRankAction={doRankAction} />
            </div>
        </div>
    )
}

export default Page
