    const lastUpdate = React.useRef(0)

    function setValue(name, value) {
        var cleanString = value.replace(
            /[`~!@#$%^&*()_|+\-=?;:'",.<>\{\}\[\]\\\/]/gi,
            ''
        )

        setValues({ ...values, [name]: cleanString })
    }

