import './App.css'
import React from 'react'
import { Button } from '@material-ui/core'
import { makeStyles, withStyles } from '@material-ui/core/styles'

import Collapse from '@material-ui/core/Collapse'
import IconButton from '@material-ui/core/IconButton'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import MuiTableCell from '@material-ui/core/TableCell'
import TableContainer from '@material-ui/core/TableContainer'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'
import KeyboardArrowDownIcon from '@material-ui/icons/KeyboardArrowDown'
import KeyboardArrowUpIcon from '@material-ui/icons/KeyboardArrowUp'

const useRowStyles = makeStyles({
    root: {
        '& > *': {
            borderBottom: 'unset',
            color: 'white',
        },
    },
    head: {
        color: 'white',
    },
})

const TableCell = withStyles({
    root: {
        borderBottom: 'none',
    },
})(MuiTableCell)

function Row(props) {
    const { row, doRankAction } = props
    const [open, setOpen] = React.useState(false)
    const classes = useRowStyles()

    return (
        <React.Fragment>
            <TableRow className={classes.root}>
                <TableCell>
                    <IconButton
                        aria-label="expand row"
                        size="small"
                        onClick={() => setOpen(!open)}
                    >
                        {open ? (
                            <KeyboardArrowUpIcon />
                        ) : (
                            <KeyboardArrowDownIcon />
                        )}
                    </IconButton>
                </TableCell>
                <TableCell align="left">{row.rank_level}</TableCell>
                <TableCell align="left">{row.rank_name}</TableCell>
            </TableRow>
            <TableRow>
                <TableCell
                    style={{ paddingBottom: 0, paddingTop: 0 }}
                    colSpan={6}
                >
                    <Collapse in={open} timeout="auto" unmountOnExit>
                        <div>
                            <Table size="small" aria-label="purchases">
                                <div
                                    style={{
                                        paddingBottom: '8px',
                                        display: 'flex',
                                        justifyContent: 'center',
                                    }}
                                >
                                    <Button
                                        style={{
                                            backgroundColor: '#358cff',
                                        }}
                                        onClick={() => {
                                            doRankAction('changeRankLevel', row)
                                        }}
                                    >
                                        Change Rank Level
                                    </Button>
                                    <Button
                                        style={{
                                            backgroundColor: '#358cff',
                                            marginLeft: '8px',
                                            marginRight: '8px',
                                        }}
                                        onClick={() => {
                                            doRankAction('changeRankName', row)
                                        }}
                                    >
                                        Change Rank Name
                                    </Button>
                                    <Button
                                        style={{
                                            backgroundColor: '#da5259',
                                        }}
                                        onClick={() => {
                                            doRankAction('removeRank', row)
                                        }}
                                    >
                                        Remove Rank
                                    </Button>
                                </div>
                            </Table>
                        </div>
                    </Collapse>
                </TableCell>
            </TableRow>
        </React.Fragment>
    )
}

function Page(props) {
    const classes = useRowStyles()

    return (
        <TableContainer className={classes.head}>
            <Table aria-label="collapsible table">
                <TableHead>
                    <TableRow>
                        <TableCell />
                        <TableCell>
                            <b>Rank Level</b>
                        </TableCell>
                        <TableCell>
                            <b>Rank Name</b>
                        </TableCell>
                    </TableRow>
                </TableHead>
                <TableBody>
                    {props.rows.map((row) => (
                        <Row
                            key={row.id}
                            row={row}
                            doRankAction={props.doRankAction}
                        />
                    ))}
                </TableBody>
            </Table>
        </TableContainer>
    )
}

export default Page
