import './App.css'
import React from 'react'
import { Button } from '@material-ui/core'
import { makeStyles, withStyles } from '@material-ui/core/styles'

import Collapse from '@material-ui/core/Collapse'
import IconButton from '@material-ui/core/IconButton'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import MuiTableCell from '@material-ui/core/TableCell'
import TableContainer from '@material-ui/core/TableContainer'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'
import KeyboardArrowDownIcon from '@material-ui/icons/KeyboardArrowDown'
import KeyboardArrowUpIcon from '@material-ui/icons/KeyboardArrowUp'

import Moment from 'react-moment'
import 'moment-timezone'

const useRowStyles = makeStyles({
    root: {
        '& > *': {
            borderBottom: 'unset',
            color: 'white',
        },
    },
    head: {
        color: 'white',
    },
})

const TableCell = withStyles({
    root: {
        borderBottom: 'none',
    },
})(MuiTableCell)

function Row(props) {
    const { row } = props
    const [open, setOpen] = React.useState(false)
    const classes = useRowStyles()

    function doAction(name, row) {
        props.doAction(name, row)
        setOpen(false)
    }

    return (
        <React.Fragment>
            <TableRow className={classes.root}>
                <TableCell>
                    <IconButton
                        aria-label="expand row"
                        size="small"
                        onClick={() => setOpen(!open)}
                    >
                        {open ? (
                            <KeyboardArrowUpIcon />
                        ) : (
                            <KeyboardArrowDownIcon />
                        )}
                    </IconButton>
                </TableCell>
                <TableCell>{row.plate}</TableCell>
                <TableCell>{row.name}</TableCell>
                <TableCell>{row.inGarage}</TableCell>
            </TableRow>
            <TableRow>
                <TableCell
                    style={{ paddingBottom: 0, paddingTop: 0 }}
                    colSpan={6}
                >
                    <Collapse in={open} timeout="auto" unmountOnExit>
                        <div>
                            <Table size="small" aria-label="purchases">
                                <div
                                    style={{
                                        paddingBottom: '8px',
                                        display: 'flex',
                                        justifyContent: 'center',
                                    }}
                                >
                                    <Button
                                        style={{ backgroundColor: '#358cff' }}
                                        onClick={() => {
                                            doAction('activityLog', row)
                                        }}
                                    >
                                        Activity Log
                                    </Button>
                                    <Button
                                        style={{
                                            backgroundColor: '#7a7e89',
                                            marginLeft: '8px',
                                        }}
                                        onClick={() => {
                                            doAction('changeRank', row)
                                        }}
                                    >
                                        Change Rank
                                    </Button>
                                    <Button
                                        style={{
                                            backgroundColor: '#7a7e89',
                                            marginLeft: '8px',
                                        }}
                                        onClick={() => {
                                            doAction('changeAlias', row)
                                        }}
                                    >
                                        Change Alias
                                    </Button>
                                    <Button
                                        style={{
                                            backgroundColor: '#da5259',
                                            marginLeft: '8px',
                                        }}
                                        onClick={() => {
                                            doAction('remove', row)
                                        }}
                                    >
                                        Remove
                                    </Button>
                                </div>
                            </Table>
                        </div>
                    </Collapse>
                </TableCell>
            </TableRow>
        </React.Fragment>
    )
}

function Page(props) {
    const classes = useRowStyles()
    const { doAction } = props

    return (
        <TableContainer className={classes.head}>
            <Table aria-label="collapsible table">
                <TableHead>
                    <TableRow>
                        <TableCell />
                        <TableCell>
                            <b>Plate</b>
                        </TableCell>
                        <TableCell>
                            <b>Model</b>
                        </TableCell>
                        <TableCell>
                            <b>Status</b>
                        </TableCell>
                    </TableRow>
                </TableHead>
                <TableBody>
                    {props.rows.map((row) => (
                        <Row key={row.vin} row={row} doAction={doAction} />
                    ))}
                </TableBody>
            </Table>
        </TableContainer>
    )
}

export default Page
