import './App.css'
import React from 'react'

function Field(props) {
  return (
    <div
      style={{
        width: '40%',
        display: 'flex',
        fontFamily: 'DM Mono',
        fontSize: '24px',
      }}
    >
      <div style={{ textAlign: 'left', width: '50%' }}>{props.name}:</div>
      <div style={{ textAlign: 'right', width: '50%' }}>{props.value}</div>
    </div>
  )
}

function FieldPadding() {
  return <div style={{ height: '8px' }} />
}

function Page(props) {
  const { doNuiAction } = props

  const [factionInfo, setFactionInfo] = React.useState([])

  React.useEffect(() => {
    doNuiAction('getFactionInfo', [], {
      faction_name: 'LSPD',
      member_count: 95,
      max_members: 99,
    }).then((resp) => {
      setFactionInfo(resp)
    })
  }, []) // eslint-disable-line

  return (
    <div className="FactionInfoMain">
      <div className="PageSectionContainer">
        <div className="FactionInfoMainText">
          <Field name="Faction Name" value={factionInfo.faction_name} />
          <FieldPadding />
          <Field name="Members" value={factionInfo.member_count} />
          <FieldPadding />
          <Field name="Max Members" value={factionInfo.max_members} />
        </div>
      </div>
    </div>
  )
}

export default Page
