import './App.css'
import React from 'react'

import { DataGrid } from '@material-ui/data-grid'
import { renderCellExpand } from './renderCellExpand.tsx'

const columns = [
    { field: 'id', headerName: 'ID', width: 75 },
    {
        field: 'character_id',
        headerName: 'CID',
        width: 75,
        sortable: false,
    },
    {
        field: 'character_name',
        headerName: 'Person',
        width: 150,
        sortable: false,
    },
    {
        field: 'action',
        headerName: 'Action Type',
        width: 200,
        sortable: false,
    },
    {
        field: 'content',
        headerName: 'Content',
        sortable: false,
        width: 375,
        renderCell: renderCellExpand,
    },
    {
        field: 'timestamp',
        headerName: 'Timestamp',
        width: 180,
        sortable: false,
    },
]

function Page(props) {
    return (
        <div style={{ height: '100%', border: 'none !important' }}>
            <DataGrid
                style={{
                    '.MuiDataGrid-row, .MuiDataGrid-root .MuiDataGrid-cell, .rendering-zone': {
                        'max-height': 'none !important',
                    },
                    '.MuiDataGrid-root .MuiDataGrid-window': {
                        position: 'relative !important',
                    },
                    '.MuiDataGrid-root .MuiDataGrid-viewport': {
                        'max-height': 'none !important',
                    },
                    '.MuiDataGrid-root': { height: 'auto !important' },
                }}
                rows={props.rows}
                columns={columns}
                pageSize={15}
                disableSelectionOnClick
                disableColumnMenu
                autoPageSize
                sortModel={[
                    {
                        field: 'id',
                        sort: 'desc',
                    },
                ]}
                filterModel={{
                    items: props.filters,
                }}
            />
        </div>
    )
}

export default Page
