function Page(props) {
    const { doNuiAction } = props

    const [factionMembers, setFactionMembers] = React.useState([])
    const [rows, setRows] = React.useState([])
    const [rank, setRank] = React.useState('')

    const [values, setValues] = React.useState({
        alias: '',
        newMemberCID: '0',
        searchBar: '',

        rankOptions: [],
    })

    function setValue(name, value) {
        console.log(`Set ${name} to ${value}`)
        setValues({ ...values, [name]: value })
    }

    function handleSearchbarVal(event) {
        const targetName = event.target.value
        setValue('searchBar', targetName)

        if (event.target.value.length === 0) {
            setRows(factionMembers.sort(sortMembers))
        } else {
            let currRows = []
            for (let i = 0; i < factionMembers.length; i++) {
                if (
                    factionMembers[i].name
                        .toLowerCase()
                        .includes(targetName.toLowerCase())
                ) {
                    currRows.push(factionMembers[i])
                }
            }
            setRows(currRows.sort(sortMembers))
        }
    }

    function reloadMembers() {
        doNuiAction(
            'getFactionMembers',
            [],
            [
                {
                    character_id: 3,
                    character_name: 'Pede Homo',
                    rank_name: 'Cadet',
                    rank_level: 3,
                    alias: '001',
                    lastLive: 1619890577,
                },
                {
                    character_id: 4,
                    character_name: 'Jah Pede',
                    rank_name: 'Officer',
                    rank_level: 4,
                    alias: '002',
                    lastLive: 1619890577,
                },
                {
                    character_id: 24,
                    character_name: 'Jah Pede',
                    rank_name: null,
                    rank_level: null,
                    alias: '002',
                    lastLive: 1619890577,
                },
            ]
        ).then((resp) => {
            let newRows = []
            for (let i = 0; i < resp.length; i++) {
                newRows.push({
                    cid: resp[i].character_id,
                    name: resp[i].character_name,
                    rank: resp[i].rank_name,
                    alias: resp[i].alias,
                    lastLive: resp[i].lastLive,
                    rank_level: resp[i].rank_level,
                })
            }
            setFactionMembers(newRows.sort(sortMembers))
            setRows(newRows.sort(sortMembers))
        })
    }

    const [targetPlr, setTargetPlr] = React.useState(false)
    const [currentModal, setCurrentModal] = React.useState(false)

    const sortRanks = function (a, b) {
        if (a.rank_level < b.rank_level) {
            return 1
        }
        if (a.rank_level > b.rank_level) {
            return -1
        }
        return 0
    }

    const sortMembers = function (a, b) {
        if (a.rank_level < b.rank_level) {
            return 1
        }
        if (a.rank_level > b.rank_level) {
            return -1
        }

        return 0
    }

    function doPlayerAction(action, player) {
        setTargetPlr(player)
        if (action === 'remove') {
            doNuiAction('removeMember', { player: player }, []).then(() => {
                reloadMembers()
            })
        } else if (action === 'changeAlias') {
            setCurrentModal('changeAlias')
            setValue('alias', player.alias)
        } else if (action === 'addMember') {
            setCurrentModal('addMember')
            setValue('newMemberCID', '')
        } else if (action === 'changeRank') {
            setRank(player.rank)
            setCurrentModal('changeRank')

            doNuiAction(
                'getFactionRanks',
                {},
                [
                    {
                        rank_name: 'Cadet',
                        rank_level: 1,
                    },
                    {
                        rank_name: 'Officer',
                        rank_level: 2,
                    },
                    {
                        rank_name: 'Senior Officer',
                        rank_level: 3,
                    },
                    {
                        rank_name: 'Sergeant',
                        rank_level: 4,
                    },
                ],
                true
            ).then((ranks) => {
                setValue('rankOptions', ranks.sort(sortRanks))
            })
        }
    }

    function closeModal() {
        setCurrentModal(false)
    }

    React.useEffect(() => {
        reloadMembers()
    }, []) // eslint-disable-line

    function setAlias(alias) {
        closeModal()
        doNuiAction(
            'setAlias',
            { player: targetPlr, alias: values.alias },
            []
        ).then(() => {
            reloadMembers()
        }, true)
    }

    function addMember() {
        closeModal()
        doNuiAction('addMember', { cid: values.newMemberCID }, []).then(() => {
            reloadMembers()
        })
    }

    function changeRank() {
        closeModal()
        doNuiAction('changeRank', { player: targetPlr, rank: rank }, []).then(
            () => {
                reloadMembers()
            },
            true
        )
    }

    return (
        <div className="FactionMembersMain">
            <div
                className="PageSectionContainer"
                style={{ width: '100%', height: '10%', alignItems: 'center' }}
            >
                <SearchBar
                    name="Members"
                    values={values}
                    handleSearchbarVal={handleSearchbarVal}
                    doPlayerAction={doPlayerAction}
                />
            </div>
            <div className="PageSectionPadding" />
            <div
                className="PageSectionContainer"
                style={{ width: '100%', height: '90%' }}
            >
                <div
                    style={{
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'center',
                    }}
                >
                    <Modal
                        className="Modal"
                        isOpen={currentModal !== false}
                        onRequestClose={closeModal}
                        style={{
                            overlay: {
                                backgroundColor: 'rgba(255, 255, 255, 0)',
                            },
                        }}
                        ariaHideApp={false} // FIX WEIRD ERRORS
                        animation={false}
                    >
                        <div className="ModalContainer">
                            {currentModal === 'changeAlias' && (
                                <ChangeAliasModal
                                    closeModal={closeModal}
                                    targetPlr={targetPlr}
                                    values={values}
                                    setValue={setValue}
                                    setAlias={setAlias}
                                />
                            )}
                            {currentModal === 'addMember' && (
                                <AddMemberModal
                                    closeModal={closeModal}
                                    targetPlr={targetPlr}
                                    values={values}
                                    setValue={setValue}
                                    addMember={addMember}
                                />
                            )}
                            {currentModal === 'changeRank' && (
                                <ChangeRankModal
                                    closeModal={closeModal}
                                    targetPlr={targetPlr}
                                    values={values}
                                    setValue={setValue}
                                    changeRank={changeRank}
                                    rank={rank}
                                    setRank={setRank}
                                />
                            )}
                        </div>
                    </Modal>
                </div>

                <MemberPageTable rows={rows} doAction={doPlayerAction} />
            </div>
        </div>
    )
}

export default Page
